<template>
    <div class="dictionary" v-if="!data.loading" v-show="!data.updating">
        <label class="title">{{ t(`pages.${props.dict}.${ props.item?.code ? 'edit' : 'add' }.title`) }}</label>


        <div class="form-line">
            <label>
                <span>
                    {{ t(`pages.${props.dict}.fields.cover`) }}
                </span>
                <span class="attention">
                    {{ t(`pages.${props.dict}.fields.min-cover-size`) }}
                </span>
            </label>

            <div class="cover-preview">
                <FileUploader 
                    class="upload"
                    icon="upload-img"
                    :acceptedFormats="['image/png', 'image/jpeg', 'image/jpg', 'image/gif', 'image/jfif']"
                    :loading="data.loading"
                    :dropZone="true"
                    :showPreview="true"
                    :preview="data.cover?.resized"
                    zoneType="square-image"
                    @change="updateCover"
                />
            </div>

        </div>

        <div class="form-line">
            <label>{{ t(`pages.${props.dict}.fields.title`) }}</label>
            <MultilingualField
                :value="data.title"
                @change="onTitleChange"
            />
        </div>

        <Teleport to="#head-buttons">
            <Button
                v-if="data.code"
                icon="trash"
                :name="t(`pages.${props.dict}.remove.title`)"
                class="size-s tertiary invert"
                @click="remove"
            />

            <Button :name="t(`pages.${props.dict}.${ props.item?.code ? 'edit' : 'add' }.save`)"
                class="size-s secondary"
                :disabled="saveDisabled"
                @click="saveChanges"
            />
        </Teleport>
    </div>
    <div class="loader" v-if="data.loading || data.updating">
        <div>
            <Loader :inside="true" class="tertiary" />
        </div>
    </div>
</template>

<script setup>
    import { reactive, computed, onMounted } from 'vue';
    import Button from 'ðŸ”—/components/ui/Button.vue';
    import MultilingualField from 'ðŸ”—/components/ui/MultilingualField.vue';
    import { useI18n } from 'vue-i18n';
    import api from 'ðŸ”—/api';
    import FileUploader from 'ðŸ”—/components/ui/FileUploader.vue';
    import Loader from 'ðŸ”—/components/ui/Loader.vue';
    import { delay, message } from 'ðŸ”—/utils/utils';
    import stores from '@/stores';
    const modals = stores.modals();

    const emit = defineEmits(['update', 'add', 'remove']);
    const { t } = useI18n();

    const props = defineProps({
        dict: { type: String, required: true },
        item: { type: [Object, null], default: null }
    });

    const data = reactive({
        code: null,
        title: {},
        cover: {},
        newCover: null,
        loading: true
    });

    const saveDisabled = computed(() => {
        return !data.title || !data.title['en-US'];
    });

    const onTitleChange = (value) => {
        data.title = value || {};
    }

    const remove = async () => {
        const fns = api.dict();

        if (!fns[props.dict]?.remove) {
            return;
        }

        modals.confirm({
            title: t(`pages.${props.dict}.remove.confirm.title`),
            body: t(`pages.${props.dict}.remove.confirm.description`, { name: data.title['en-US'] }),
            onOk: async () => {
                await fns[props.dict].remove(data.code);

                emit('remove', data.code);
                modals.open('success', {
                    data: {
                        title: t(`pages.${props.dict}.remove.done.title`),
                        close: t(`pages.${props.dict}.remove.done.close`)
                    },
                    quietClose: true
                });
            }
        }); 
    }

    const saveChanges = async () => {
        const fns = api.dict();

        if (!fns[props.dict]?.add) {
            return;
        }

        if (!fns[props.dict]?.update) {
            return;
        }

        data.updating = true;

        let payload;

        if (data.code) {
            payload = await fns[props.dict].update(data);
        } else {
            payload = await fns[props.dict].add(data);
        }

        await delay(200);
        data.updating = false;

        if (payload?.code) {
            message.info(t(`pages.${props.dict}.${ data.code ? 'edit' : 'add' }.done.title`));
            // modals.open('success', {
            //     data: {
            //         timer: 3,
            //         title: t(`pages.${props.dict}.${ data.code ? 'edit' : 'add' }.done.title`),
            //         close: t(`pages.${props.dict}.${ data.code ? 'edit' : 'add' }.done.close`)
            //     },
            //     quietClose: true
            // });
        }

        if (data.code) {
            emit('update', payload);
        } else {
            emit('add', payload);
        }

        data.code = payload.code;
        data.cover = payload.cover || {};
        data.newCover = null;
    }

    const updateCover = (e) => {
        if (e?.name) {
            data.newCover = e;
        }
    }

    const getItem = async () => {
        data.loading = true;

        const fns = api.dict();

        if (!fns[props.dict]?.get) {
            return;
        }

        const payload = await fns[props.dict].get(props.item.code);

        data.code = payload?.code;
        data.title = payload?.title || {};
        data.cover = payload?.cover || {};

        await delay(200);

        data.loading = false;
    }

    onMounted(() => {
        if (props.item?.code) {
            getItem();
        } else {
            data.loading = false;
        }
    });
</script>

<style lang="scss" scoped>
    .loader {
        @include inside-loader;
    }

    .dictionary {
        display: flex;
        flex-direction: column;
        gap: 24px;
        .title {
            font: var(--font-h3);
            font-weight: bold;
            color: var(--dark-500);
        }
    }

    .form-line {
        @include studio.form-line();

        .cover-preview {
            position: relative;
            background-size: cover;
            background-position: center;
            border-radius: 10px;
            img {

            }
        }
    }

    // .buttons {
    //     display: flex;
    //     flex-direction: row;
    //     align-items: center;
    //     gap: 8px;
    //     margin-top: 32px;
    // }
</style>
