<template>
    <PageHeader :title="t(`pages.${ props.dict }.title`)" :detail="!!data.item">
        <Button
            v-if="!data.item"
            icon="plus"
            @click="add"
            class="size-s"
            :name="t(`pages.${ props.dict }.add.title`)"
        />

        <IconButton
            v-else
            icon="close"
            @click="data.item = null"
            class="size-s tertiary invert"
            :name="t(`pages.${ props.dict }.cancel`)"
        />  
    </PageHeader>

    <div class="box">
        <Dictionaries
            :id="data.item?.code"
            :sort="props.sort"

            ref="dictionaries"
            :dict="props.dict"

            @change="onSelectItem" />

        <div v-if="data.item" class="item-detail">
            <DictionaryInfo
                :dict="props.dict"
                :item="data.item"
                @add="onAdd"
                @update="onUpdate"
                @remove="onRemove"
            />
        </div>
    </div>
</template>

<script setup>
    import { reactive } from 'vue';
    import Button from 'ðŸ”—/components/ui/Button.vue';
    import IconButton from 'ðŸ”—/components/ui/IconButton.vue';
    import PageHeader from '@/components/blocks/PageHeader/PageHeader.vue';
    import Dictionaries from '@/components/blocks/Dictionaries.vue';
    import { ref } from 'vue'

    import DictionaryInfo from '@/components/blocks/DictionaryInfo.vue';
    
    import { useI18n } from 'vue-i18n';

    const props = defineProps({
        dict: { type: String, required: true },
        sort: { type: Array, default: () => ([]) }
    });

    const { t } = useI18n();
    const dictionaries = ref(null)

    const data = reactive({
        item: null
    });

    const onSelectItem = (item) => {
        data.item = null;
        setTimeout(() => {
            data.item = item?.code ? item : null;
        });
    }

    const add = () => {
        data.item = null;
        setTimeout(() => {
            data.item = {};
        });
    }

    const onRemove = (code) => {
        data.item = null;
        dictionaries.value.remove(code)
    }

    const onUpdate = (payload) => {
        dictionaries.value.update(payload)
    }

    const onAdd = (payload) => {
        dictionaries.value.add(payload)
    }

</script>

<style lang="scss" scoped>
    @include studio.default-box;
</style>
