<template>
    <div class="items-list">
        <div class="filters">
            <Field
                icon="search"
                :placeholder="t('blocks.dictionaries.search')"
                class="noerror light"
                @change="onSearchChange"
            />

            <Sort
                v-if="props.sort?.length"
                :sort="props.sort"
                :code="data.sort?.code"
                @change="setSort" />

            <ViewMode
                v-if="!dict.viewModes[props.dict] || dict.viewModes[props.dict].length > 1"
                :code="props.dict"
                :modes="dict.viewModes[props.dict]"
                @change="data.viewMode = $event" />
        </div>

        <ul :class="['items', data.viewMode]" v-if="data.items.length">
            <li v-for="(item, id) in data.items"
                :class="{ active: props.id === item.code }"
                @click="emit('change', item)"
            >
                <span class="dictionary-preview">
                    <Img v-if="item.cover?.resized || defaultImg?.resized"
                        :preview="item.cover?.resized || defaultImg?.resized"
                        :original="item.cover?.original || defaultImg?.original"
                        :alt="item?.title"
                        :wathermark="data.viewMode === 'list' ? null : props.dict"
                    >
                        <span class="track-count">
                            <Icon icon="note" />
                            <span>{{ item?.trackCount || 0 }}</span>
                        </span>
                        {{ item?.title }}
                    </Img>
                    <span v-if="data.viewMode === 'list'">
                        {{ item.title }}
                        <span class="track-count">
                            <Icon icon="note" />
                            <span>{{ item?.trackCount || 0 }}</span>
                        </span>
                    </span>
                </span>
            </li>
        </ul>

        <Button :loading="data.loading"
            class="tertiary size-l"
            :name="t(`blocks.dictionaries.load`)"
            v-if="data.loading || (data.items.length < data.total)"
            @click="getItems"
        />

        <EmptyLabel
            v-if="!data.items.length && !data.loading"
            class="empty"
            :title="t(`pages.${props.dict}.empty.title`)"
            :description="t(`pages.${props.dict}.empty.description`)"
        />
    </div>
</template>

<script setup>
    import { useI18n } from 'vue-i18n';
    import { onMounted, reactive, computed } from 'vue';
    import api from 'ðŸ”—/api';
    import Sort from 'ðŸ”—/components/ui/Sort.vue';
    import ViewMode from 'ðŸ”—/components/ui/ViewMode.vue';
    import Button from 'ðŸ”—/components/ui/Button.vue';
    import EmptyLabel from 'ðŸ”—/components/ui/EmptyLabel.vue';
    import Field from 'ðŸ”—/components/ui/Field.vue';
    import Img from 'ðŸ”—/components/ui/Img.vue';

    const dict = api.dict();
    const emit = defineEmits(['change']);

    const { t } = useI18n();

    const props = defineProps({
        dict: { type: String, required: true },
        sort: { type: Array, default: () => ([]) },
        id: { type: [String, null], default: null }
    });

    const data = reactive({
        items: [],
        loading: false,
        skip: 0,
        take: 30, // Temp
        total: 0,
        sort: null,
        searchStr: null,
        viewMode: dict.viewModes[props.dict]?.length > 1 ? localStorage[`view-mode-${props.dict}`] || 'list' : dict.viewModes[props.dict][0] || 'list'
    });

    const defaultImg = computed(() => {
        const fns = api.dict();

        if (!fns[props.dict]?.defaultCover) {
            return;
        }

        return fns[props.dict]?.defaultCover();
    });

    const setSort = (item) => {
        if (data.sort?.code === item.code) {
            data.sort = null;
        } else {
            data.sort = item;
        }

        reload();
    };

    const onSearchChange = async (e) => {
        data.searchStr = e.target.value?.toLowerCase().trim() || '';
        data.skip = 0;

        clearTimeout(data.searchTimeout);

        data.searchTimeout = setTimeout(() => {
            delete data.searchTimeout;
            reload();
        }, 200);
    }

    const getItems = async () => {
        const fns = api.dict();

        if (!fns[props.dict]?.list) {
            return;
        }

        data.loading = true;

        if (!data.skip) {
            data.items = [];
        }

        const params = {
            skip: data.skip,
            take: data.take
        };

        if (data.sort) {
            params.sort = data.sort.code;
            params.sort_dir = data.sort.direction;
        }

        if (!!data.searchStr?.trim().length) {
            params.title = data.searchStr.toLowerCase().trim();
        }

        const payload = await fns[props.dict].list(params) || [];

        if (!payload) {
            data.loading = false;
        }

        setTimeout(() => {
            data.items.splice(data.items.length, 0, ...(payload.items || []));

            data.skip = data.items.length;
            data.total = payload.total;

            data.loading = false;

            // data.items.forEach(item => {
            //     fns[props.dict].remove(item.code, props.dict)
            // })

            // window.add = fns[props.dict].add;
        }, 200);
    };

    const reload = () => {
        data.skip = 0;
        getItems();
    };

    const add = (payload) => {
        data.items.unshift(payload);
        data.total += 1;
    }

    const update = (payload) => {
        if (!payload?.code) {
            return;
        }

        const item = data.items.find(item => item.code === payload.code);
        if (item) {
            data.items.splice(data.items.indexOf(item), 1, payload);
        }
    }

    const remove = (code) => {
        if (!code) {
            return;
        }

        const item = data.items.find(item => item.code === code);
        if (item) {
            data.items.splice(data.items.indexOf(item), 1);
            data.skip -= 1;
            data.total -= 1;
        }
    }

    defineExpose({
        remove,
        add,
        update,
        reload
    });

    onMounted(() => {
        getItems();
    });
</script>

<style lang="scss" scoped>
    @include studio.default-items-list;
    
    .items-list {
        .items {
            li {
                .dictionary-preview {
                    @include dictionary-list-preview;
                    .wathermark,
                    img {
                        width: 46px;
                        height: 46px;
                    }

                    & > span {
                        justify-content: space-between;
                    }

                    .track-count {
                        display: flex;
                        flex-direction: row-reverse;
                        gap: 4px;
                        align-items: baseline;
                        font: var(--font-caption);
                        margin-left: auto;

                        svg {
                            fill: var(--white-500);
                            stroke: none;
                            width: 14px;
                            height: 14px;
                        }
                        span {
                            text-shadow: none;
                        }
                    }
                }
            }

            &.list {
                li {
                    .dictionary-preview {
                        & > span {
                            justify-content: space-between;
                        }

                        .track-count {
                            margin-left: 0;
                            svg {
                                fill: var(--dark-20);
                            }
                            span {
                                color: var(--dark-20);
                            }
                        }
                    }
                }
            }
            &.dashboard {
                flex-direction: row;
                flex-wrap: wrap;
                gap: 16px 4px;
                li {
                    padding: 4px;
                    border-radius: 10px;
                    .dictionary-preview {
                        @include dictionary-dashboard-preview;
                    }

                    &.active {
                        box-shadow: 0px 0px 2px 0px var(--gray-80);
                        .dictionary-preview {
                            img {
                                border: solid 1px var(--primary-100);
                            }
                        }
                    }
                }
            }
        }
    }
</style>